.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70vw;
  margin: 0 auto;
}

.form__title {
  text-align: center;
  padding: 20px 0;
  color: var(--font-color);
}

.form__buttons > button:not(:last-child) {
  margin-right: 20px;
}

.button {
  border: 3px solid var(--accent-color);
  border-radius: 10px;
  font-size: 16px;
  font-weight: 550;
  letter-spacing: 1px;
  padding: 10px 20px;
  margin-top: 10px;
  outline: none;
  cursor: pointer;
}

.button.--primary {
  color: white;
  background-color: var(--accent-color);
}

.button.--secondary {
  color: var(--accent-color);
  background-color: transparent;
}
